import React from "react";

import Divider from "../Divider/Divider";
import Pattern from "../Pattern/Pattern";
import { colors } from "../styleguide";

import {
	BackgroundImage,
	StyledBadge,
	StyledCard,
	StyledIcon,
	StyledIconWrapper,
	StyledImage,
} from "./ObjectCreationCard.styles";
import { ObjectCreationCardProps } from "./ObjectCreationCard.types";


const ObjectCreationCard = ({
	dataQA,
	ariaLabel,
	backgroundColor,
	pattern,
	patternColor,
	thumbnailImageURL,
	className,
	badge,
	icon,
	viewBox = "0 0 1000 525",
	topSideHeight,
	children,
}: ObjectCreationCardProps) => (
	<StyledCard
		dataQA={dataQA}
		ariaLabel={ariaLabel}
		isClickable={true}
		padding="0px"
		className={className}
	>
		<BackgroundImage
			aria-label={`${ariaLabel} background`}
			topSideHeight={topSideHeight}
			backgroundColor={backgroundColor}
		>
			{pattern &&
				<Pattern name={pattern} patternColor={patternColor} backgroundColor={backgroundColor} viewBox={viewBox} />
			}
			{thumbnailImageURL &&
				<StyledImage data-qa={`${thumbnailImageURL}-create-card`}>
					<img src={thumbnailImageURL} alt={thumbnailImageURL} />
				</StyledImage>}
			{(!thumbnailImageURL && icon) &&
				<StyledIconWrapper>
					<StyledIcon
						aria-label={`${ariaLabel} icon`}
						strokeWidth={2}
						name={icon}
						size={20}
						color={colors.white}
					/>
				</StyledIconWrapper>
			}
			{badge && <StyledBadge {...badge} />}
		</BackgroundImage>

		<Divider dataQA="create-card-divider" />
		{children}
	</StyledCard>
);

ObjectCreationCard.displayName = "ObjectCreationCard";
export default ObjectCreationCard;
